.timeline {
  padding: 0 40px 40px 40px;
  margin-top: 5px;
  font-family: 'Lato', "PingFang SC", "Microsoft YaHei", sans-serif;
}
@media screen and (max-width: 738px) {
  .timeline {
    padding: 0 20px 20px 20px ;
  }
}
.timeline .list-title {
  font-size: 22px;
  position: relative;
  top: -5px;
}
.timeline .list-title .type-name {
  color: #bbb;
  margin-left: 5px;
}
